import React, { Component } from 'react'

import { Fragment } from 'react'
import { MdAdd } from 'react-icons/md'
import { Link, Redirect } from 'react-router-dom'
import Modal, { Themes, ModalSize } from '../../components/Modal/Modal'
import ChooseExamCenter from '../Application/ExamCenter/ChooseExamCenter'
import * as Interfaces from '../../interfaces/index';
import { connect } from "react-redux";
import {
  Todo,
  Auth,
  fetchTodos,
  deleteTodo,
  fetchAuth,
  FC_GetTwoFactorAuth,
  FC_ChangeTwoFactorType,
  FC_Login,
  FC_LoginCheckCode,
  addAnswer,
  SubmitAnswers,
  loadExam,
  examDataInterface,
  examAnswer,
} from "../../actions";
import { StoreState } from "../../reducers";
import Loading from '../../components/Loading/Loading'
import { ExamInterface, examStatus } from '../../interfaces/exam'
import { exam_question, question_answer } from '../../interfaces/exam_question'
import { newAnswer } from '../DoExam/DoExam'
import DoExamResults from '../DoExam/DoEamResults/DoExamResults'
import { findResult } from '../DoExam/utils/utils'
import { PositionAPI } from '../../interfaces/index'
import { application_exam } from '../../interfaces/application'

interface PostExamsProps {
  doExam: examDataInterface;
  auth: Auth;
  loadExam: (user_id: string, exam: ExamInterface) => void;
  selectedPosition: PositionAPI | null;
  selectedExams: application_exam[] | null;
}
interface PostExamsState {
  appointmentDetailsModal: {
    status: boolean;
    item: ExamInterface | null;
  };
  redirect: boolean;
  displayResults: boolean;
}

export class _PostExams extends Component<PostExamsProps, PostExamsState> {
  constructor(props: PostExamsProps) {
		super(props);

		this.state = {
      appointmentDetailsModal: {
        status: false,
        item: null,
      },
      redirect: false,
      displayResults: false,
    };
	}

  renderExam = (user_id: string, exam: ExamInterface) => {
    exam.status = examStatus.Editing;
    this.props.loadExam(user_id, exam);
    console.log("Selected exam: ", user_id, exam);
    this.setState({ redirect: true });
  }

  componentDidMount = () => {
    if (this.props.doExam.exam_answers) {
      if (this.props.doExam.exam_answers.status === examStatus.Editing) {
        this.setState({ redirect: true });
      }
    }
    console.log('====================================');
    console.log("selected position: ", this.props.selectedPosition);
    console.log('====================================');
    console.log('====================================');
    console.log("Exams: ", this.props.doExam.examsList);
    console.log('====================================');
  }

  getLocation = (course_id: string) => {
    return `
    Province: ${this.props.selectedExams !== null && this.props.selectedExams.find(itm => itm.courseId === course_id)?.location.province_name}, 
    District: ${this.props.selectedExams !== null && this.props.selectedExams.find(itm => itm.courseId === course_id)?.location.district_name},
    Center: ${this.props.selectedExams !== null && this.props.selectedExams.find(itm => itm.courseId === course_id)?.location.center_name}
    `;
  }

  selectedLocation = (course_id: string): { province: any, district: any, center: any, room: any } | false => {
    if (this.props.selectedExams !== null) {
      return {
        province: this.props.selectedExams.find(itm => itm.courseId === course_id)?.location.province_name, 
        district: this.props.selectedExams.find(itm => itm.courseId === course_id)?.location.district_name,
        center: this.props.selectedExams.find(itm => itm.courseId === course_id)?.location.center_name,
        room: this.props.selectedExams.find(itm => itm.courseId === course_id)?.location.center_name
      }
    }
    else {
      return false
    }
  }

  numberOfExams = () => {
    let count = 0;
    if (this.props.selectedExams !== null) {
      for (let x in this.props.selectedExams) {
        for (let i in this.props.doExam.examsList) {
          if (this.props.doExam.examsList[i].course.course_id === this.props.selectedExams[x].courseId) {
            count = count + 1;
          }
        }
      }
    }
    return count;
  }

  render() {
    if (this.state.redirect === true) {
      return <Redirect to="/do-exam" />
    }
    return (
      <Fragment>
        <div className="">
          {console.log("Exams: ", this.props.doExam)}
          <div className="bg-white border-b-2 border-blue-600 px-3 md:px-5 py-3 md:py-5">
            <div className="flex flex-col">
              <div className="flex flex-row justify-between items-center">
                <span className="text-blue-700 text-md md:text-xl lg:text-2xl mb-2 font-bold">{this.props.selectedPosition?.position_title}</span>
              </div>
              <div className="grid grid-cols-2 md:grid-cols-2 lg:grid-cols-4 gap-2 md:gap-5 lg:gap-10">
                <div className="col-span-1">
                  <div className="flex flex-col">
                    <span className="text-xs md:text-md font-light text-blue-700">Total exams</span>
                    <span className="text-xl md:text-3xl font-bold text-blue-700">00{this.numberOfExams()}</span>
                  </div>
                </div>
                <div className="col-span-1">
                  <div className="flex flex-col">
                    <span className="text-xs md:text-md font-light text-blue-700">New exams</span>
                    <span className="text-xl md:text-3xl font-bold text-blue-700">00{this.numberOfExams()}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="my-5 px-2">
            <div className="grid md:grid-cols-1 lg:grid-cols-2 gap-3">
              {this.props.doExam.examsList === null ? <Loading /> : 
              this.props.doExam.examsList.length === 0 ? "No Exams found!" :
              this.props.doExam.examsList.map((item, i) => (
                this.props.selectedExams !== null && this.props.selectedExams.find(itm => itm.courseId === item.course.course_id) &&
                <div className={`px-4 py-3 bg-white rounded border ${item.status === examStatus.Pending ? "border-blue-600" : "border-gray-200"} flex flex-col cursor-pointer font-normal hover:font-bold hover:border-blue-500`}>
                  <span className="text-gray-500 text-xs">Exam name</span>
                  <span className="font-bold text-blue-600 text-xl truncate">{item.course.course_code}-{item.course.course_name}</span>
                  <div className="flex flex-col text-xs text-gray-600 mt-3 gap-2">
                    <div className="flex flex-row gap-2">
                      <span className="text-gray-500">Exam date:</span>
                      <span className="text-black font-bold">02/08/2021</span>
                    </div>
                    <div className="flex flex-row gap-2">
                      <span className="text-gray-500">Location:</span>
                      <span className="text-black font-bold truncate">{this.getLocation(item.course.course_id)}</span>
                    </div>
                    <div className="flex flex-row gap-2">
                      <span className="text-gray-500">Proctoring type:</span>
                      <span className="text-blue-600 font-bold">{item.proctoring_type}</span>
                    </div>
                    <div className="flex flex-col">
                      <div className="my-2 flex flex-row justify-between items-center gap-2">
                        <div onClick={() => this.setState({ appointmentDetailsModal: { status: true, item: item} })} className="btn border border-blue-500 text-blue-600 cursor-pointer hover:bg-blue-600 hover:text-white">Details</div>
                        <div className="flex flex-col w-full">
                          {item.status === examStatus.Waiting ? 
                          <>
                            <div className="flex flex-row justify-between items-center">
                              <span className="text-xs text-blue-700">Count down: 12:00:02</span>
                              <span className="text-xs text-blue-700 italic">67%</span>
                            </div>
                            <div className="rounded-full h-3 bg-blue-400 mb-2 mt-1 flex items-center">
                              <div className="h-2 bg-blue-200 rounded-full" style={{width: '32%'}}></div>
                            </div>
                          </> :
                          item.status === examStatus.Pending &&
                          <div>
                            <div onClick={() => this.props.auth.user.id !== undefined && this.renderExam(this.props.auth.user.id, item)} className="btn bg-blue-600 text-white w-max float-right">Start exam</div>
                          </div>
                          }
                          {item.status === examStatus.Done && 
                          <div className={`w-full px-3 py-2`}>
                            <span>{findResult(item.exam_questions, item.exam_questions_answers.questionAnswers) >= 50 ? "You passed with:" : "You failed with:"} </span>
                            <span className={`w-full font-bold px-3 py-1 text-sm ${findResult(item.exam_questions, item.exam_questions_answers.questionAnswers) >= 50 ? "bg-green-100 text-green-700 border border-green-700" : "bg-red-100 text-red-700 border border-red-700"}`}>{findResult(item.exam_questions, item.exam_questions_answers.questionAnswers)}%</span>
                            <span> scores</span>
                          </div>}
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              ))
            }
            </div>
          </div>
        </div>
        {this.state.appointmentDetailsModal.status === true &&
        <Modal
          title={<div className="text-blue-600 font-bold text-2xl">{this.state.appointmentDetailsModal.item !== null && <>{this.state.appointmentDetailsModal.item.course.course_code}-{this.state.appointmentDetailsModal.item.course.course_name}</>}</div>}
          backDrop={true}
          theme={Themes.default}
          close={() => this.setState({ appointmentDetailsModal: { status: false, item: null}, displayResults: false })}
          backDropClose={false}
          widthSizeClass={ModalSize.extraLarge}
          displayClose={true}
          padding={{title: true,}} >
          {this.state.appointmentDetailsModal.item !== null &&
          <>
            <div className="px-4 py-3 pb-6 bg-gray-100">
              <div className="py-2 mb-2">
                <span className="text-xl font-bold">Exam details</span>
              </div>
              <div className="grid md:grid-cols-2 text-sm gap-2">
                <div className="col-span-1 flex flex-row items-center gap-3">
                  <span className="text-gray-500">Course name:</span>
                  <span className="font-semibold">{this.state.appointmentDetailsModal.item !== null && <>{this.state.appointmentDetailsModal.item.course.course_code}-{this.state.appointmentDetailsModal.item.course.course_name}</>}</span>
                </div>
                <div className="col-span-1 flex flex-row items-center gap-3">
                  <span className="text-gray-500">Exam Room ID:</span>
                  <span className="font-semibold">{this.state.appointmentDetailsModal.item.exam_location.room}</span>
                </div>
                <div className="col-span-1 flex flex-row items-center gap-3">
                  <span className="text-gray-500">Exam date:</span>
                  <span className="font-semibold">{"Date here"}</span>
                </div>
                <div className="col-span-1 flex flex-row items-center gap-3">
                  <span className="text-gray-500">Proctoring type:</span>
                  <span className="font-semibold bg-blue-100 text-blue-600 px-2 rounded-full">{this.state.appointmentDetailsModal.item.proctoring_type}</span>
                </div>
                <div className="col-span-1 flex flex-row items-center gap-3">
                  <span className="text-gray-500">Duration:</span>
                  <span className="font-semibold">{this.state.appointmentDetailsModal.item.duration} hours</span>
                </div>
                <div className="col-span-1 flex flex-row items-center gap-3">
                  <span className="text-gray-500">Exam center/Room:</span>
                  <span className="font-semibold">{this.state.appointmentDetailsModal.item.exam_location.center}, {this.state.appointmentDetailsModal.item.exam_location.room}</span>
                </div>
                <div className="col-span-1 flex flex-row items-center gap-3">
                  <span className="text-gray-500">Location:</span>
                  <span className="font-semibold">{this.state.appointmentDetailsModal.item.exam_location.province}, {this.state.appointmentDetailsModal.item.exam_location.district}</span>
                </div>
                <div className="col-span-1 flex flex-row items-center gap-3">
                  <span className="text-gray-500">Academic year:</span>
                  <span className="font-semibold">{this.state.appointmentDetailsModal.item.academic_year}</span>
                </div>
                <div className="col-span-1 flex flex-row items-center gap-3">
                  <span className="text-gray-500">Status:</span>
                  <span className="text-gray-500 font-bold">{this.state.appointmentDetailsModal.item.status} for exam</span>
                </div>

                <div className="col-span-1 flex flex-row items-center gap-3">
                  {this.state.appointmentDetailsModal.item.status === examStatus.Done && 
                    <div className={`w-full px-3 py-2 bg-white rounded font-bold`}>
                      <span>{findResult(this.state.appointmentDetailsModal.item.exam_questions, this.state.appointmentDetailsModal.item.exam_questions_answers.questionAnswers) >= 50 ? "You passed with:" : "You failed with:"} </span>
                      <span className={`w-full font-bold px-3 py-1 text-sm ${findResult(this.state.appointmentDetailsModal.item.exam_questions, this.state.appointmentDetailsModal.item.exam_questions_answers.questionAnswers) >= 50 ? "bg-green-100 text-green-700 border border-green-700" : "bg-red-100 text-red-700 border border-red-700"}`}>{findResult(this.state.appointmentDetailsModal.item.exam_questions, this.state.appointmentDetailsModal.item.exam_questions_answers.questionAnswers)}%</span>
                      <span> scores</span>
                    </div>}
                </div>

              </div>
            </div>
            {this.state.displayResults === true && this.state.appointmentDetailsModal.item.exam_questions_answers.questionAnswers.length !== 0 &&
            <div className="border-2 border-blue-600 px-3 pb-3 rounded mx-3 my-3">
              <DoExamResults
                page="appointmentDashboard"
                answeredQuestions={this.state.appointmentDetailsModal.item.exam_questions_answers.questionAnswers}
                testAnswers={this.state.appointmentDetailsModal.item.exam_questions_answers.questionAnswers.length === undefined ? 0 : this.state.appointmentDetailsModal.item.exam_questions_answers.questionAnswers.length}
              />
            </div>
            }
            <div className="bg-white px-4 py-4 flex gap-3 justify-between">
              <div className="flex gap-3">
                <div className="btn border border-gray-500 text-gray-500 w-max cursor-pointer">Print</div>
                {this.state.appointmentDetailsModal.item.status === examStatus.Waiting &&
                <div className="btn border border-blue-500 text-blue-500 w-max cursor-pointer hover:text-white hover:bg-blue-600">Edit exam room</div>}
              </div>
              {this.state.appointmentDetailsModal.item.status === examStatus.Pending &&
              <div onClick={() => this.props.auth.user.id !== undefined && this.state.appointmentDetailsModal.item !== null && this.renderExam(this.props.auth.user.id, this.state.appointmentDetailsModal.item)} className="btn bg-blue-600 text-white w-max cursor-pointer float-right">Start exam</div>}
              {this.state.appointmentDetailsModal.item.status === examStatus.Waiting &&
                <div className="flex flex-col w-full">
                  <div className="flex flex-row justify-between items-center">
                    <span className="text-xs text-blue-600 font-bold">Count down: 12:00:02</span>
                    <span className="text-xs text-blue-600 italic">67%</span>
                  </div>
                  <div className="rounded-full h-3 bg-blue-400 mb-2 mt-1 flex items-center w-full">
                    <div className="h-2 bg-blue-200 rounded-full" style={{width: '32%'}}></div>
                  </div>
                </div>}
                {this.state.appointmentDetailsModal.item.status === examStatus.Done &&
                <div onClick={() => this.setState({ displayResults: !this.state.displayResults })} className="btn bg-blue-600 text-white cursor-pointer">{this.state.displayResults === true ? "Close exam results" : "View Exam results"}</div>}
            </div>
          </>}
        </Modal>}
      </Fragment>
    )
  }
}


const mapStateToProps = ({
  todos,
  auth,
  doExam,
}: StoreState): { todos: Todo[]; auth: Auth; doExam: examDataInterface } => {
  return { todos: todos, auth: auth, doExam: doExam };
};

const PostExams = connect(mapStateToProps, {
  fetchTodos,
  deleteTodo,
  fetchAuth,
  FC_GetTwoFactorAuth,
  FC_ChangeTwoFactorType,
  FC_Login,
  FC_LoginCheckCode,
  addAnswer,
  SubmitAnswers,
  loadExam,
})(_PostExams);

export default PostExams;