import React, { useState } from 'react'
import { HiDocumentText } from 'react-icons/hi'
import { MdLocationOn } from 'react-icons/md';
import { course } from '../../../interfaces/course';
import { locationInterface, Locations } from '../../../interfaces/location';
import searchData from "../../../utils/search";

export interface LocationTableInterface {
  province_id: string;
  province_name: string;
  district_id: string;
  district_name: string;
  center_id: string;
  center_name: string;
  room_id: string;
  room_name: string;
  capacity: number;
  vacants: number;
}

interface ChooseExamCenterProps {
  addLocation: (
    course_id: string, 
    course_name: string,
    course_code: string,
    location: LocationTableInterface,
    exam_id: string,
  ) => void;
  positionCourses: course;
  closeModal: () => any;
}

const ChooseExamCenter = (props: ChooseExamCenterProps) => {
  const [search, setSearch] = useState<string | "">("");
  const LocationTable = (LocationData: locationInterface[]): LocationTableInterface[] | [] => {
    let locationTemp: LocationTableInterface[] = [];
    for (let p in LocationData) {
      let districts = LocationData[p].districts;
      for (let d in districts) {
        let centers = districts[d].centers;
        for (let c in centers) {
          let rooms = centers[c].rooms;
          for (let r in rooms) {
            locationTemp.push({
              province_id: LocationData[p].province_id,
              province_name: LocationData[p].province_name,
              district_id: districts[d].district_id,
              district_name: districts[d].district_name,
              center_id: centers[c].center_id,
              center_name: centers[c].center_name,
              room_id: rooms[r].room_id,
              room_name: rooms[r].room_name,
              capacity: rooms[r].capacity,
              vacants: Math.round(Math.random()*10),
            })
          }
        }
      }
    }
    return locationTemp;
  }
  return (
    <div className="w-full">

      <div className="flex flex-row items-center">
        <div>
          <MdLocationOn className="text-6xl text-blue-500" />
        </div>
        <div className="flex flex-col justify-start">
          <span className="text-2xl font-bold text-primary">Exam: {props.positionCourses.course_code}-{props.positionCourses.course_name}</span>
          <span className="text-sm text-gray-500">Choose location for the exam</span>
        </div>
      </div>

      <div className="flex flex-row px-3 gap-3 my-3 items-center">
        <div><span className="text-gray-600 text-sm">Filter by location</span></div>
        <div className="flex flex-row gap-3">
          <select placeholder="Select Province" className="input-white bg-white border border-gray-500">
            <option value="Kigali city">Kigali City</option>
            <option value="Kigali city">South</option>
            <option value="Kigali city">East</option>
            <option value="Kigali city">North</option>
            <option value="Kigali city">West</option>
          </select>
          <input onChange={(e) => setSearch(e.target.value)} type="text" placeholder="Select District" className="input-white bg-white border border-gray-500" />
          <input type="text" placeholder="Select an exam center" className="input-white bg-white border border-gray-500" />
        </div>
      </div>

      <div className="w-full">
        <div className="flex flex-row gap-3 w-full justify-around border-b-2 border-blue-600 py-4 bg-blue-100 text-blue-900 items-center">
          <div className="w-1/12 text-center font-bold">#</div>
          <div className="w-1/6 font-bold">District</div>
          <div className="w-1/6 font-bold">Center</div>
          <div className="w-1/6 font-bold">Room</div>
          <div className="w-1/6 font-bold">Capacity</div>
          <div className="w-1/6 font-bold">Vacant</div>
          <div className="w-1/6 font-bold"></div>
        </div>
          {searchData(LocationTable(Locations), search).map((item, i) => (
            <div onClick={() => { props.addLocation(props.positionCourses.course_id, props.positionCourses.course_name, props.positionCourses.course_code, item, `${i + 1}`); props.closeModal();}} key={i + 1} className="flex flex-row gap-3 w-full justify-start border-b text-gray-600 border-gray-300 py-1 text-left items-center cursor-pointer hover:bg-blue-600 hover:text-white text-sm">
            <div className="w-1/12 text-center">{i + 1}</div>
            <div className="w-1/6">{item.district_name}</div>
            <div className="w-1/6">{item.center_name}</div>
            <div className="w-1/6">{item.room_name}</div>
            <div className="w-1/6">{item.capacity}</div>
            <div className="w-1/6">{item.vacants}</div>
            <div className="w-1/6 px-3">
              {props.positionCourses !== undefined &&
                <button className="h-full px-2 py-1 float-right bg-blue-100 text-blue-700 hover:bg-blue-600 hover:border-blue-700 hover:text-white border rounded border-blue-400">Choose</button>}
            </div>
          </div>
          ))}
      </div>
    </div>
  )
}

export default ChooseExamCenter
