import React, { Component, Fragment } from 'react'
import Modal, { Themes, ModalSize } from '../../../components/Modal/Modal';
import { ApplicationStepsTypes } from '../ApplicationSteps/ApplicationSteps';
import { HiDocumentText } from 'react-icons/hi';
import ChooseExamCenter, { LocationTableInterface } from './ChooseExamCenter';
import * as Interfaces from '../../../interfaces/index';
import { Link, Redirect } from 'react-router-dom';
import { connect } from "react-redux";
import {
  Todo,
  Auth,
  fetchTodos,
  deleteTodo,
  fetchAuth,
  FC_GetTwoFactorAuth,
  FC_ChangeTwoFactorType,
  FC_Login,
  FC_LoginCheckCode,
  ActionTypes,
  examDataInterface,
  loadExam,
  addExamAnswer,
  deleteExamAnswers,
  SubmitExamAnswers,
  initiateApplication,
  selectPosition,
  createApplication,
  applicationStoreInterface,
  addLocation,
} from "../../../actions";
import { StoreState } from "../../../reducers";
import { course } from '../../../interfaces/index';
import { ApplicationInterface, ApplicationStatus } from '../../../interfaces/application';
import { MdLocationOn } from 'react-icons/md';

interface ExamCenterState {
  centersModal: boolean,
  selectedCourse: course | null,
  redirect: boolean;
}

interface ExamCenterProps {
  currentPosition: Interfaces.PositionAPI | null;
  setCurrentPosition: (position: Interfaces.PositionAPI | null) => void;
  setApplicationStep: (step: ApplicationStepsTypes) => void;
  application: applicationStoreInterface;
  addLocation: (
    course_id: string, 
    course_name: string,
    course_code: string,
    location: LocationTableInterface,
    exam_id: string,
  ) => void;
  auth: Auth;
  createApplication : (user_id: string, application: ApplicationInterface) => void;
}

export class _ExamCenter extends Component<ExamCenterProps, ExamCenterState> {
  constructor(props: ExamCenterProps) {
		super(props);

		this.state = {
      centersModal: false,
      selectedCourse: null,
      redirect: false,
    };
	}
  closeModal = () => {
    this.setState({ centersModal: false });
  }
  courseStatus = (course: course) => {
    if (this.props.application.apply?.selectedLocation.find(itm => itm.courseId === course.course_id)) {
      return this.props.application.apply?.selectedLocation.find(itm => itm.courseId === course.course_id);
    }
    // return false;
  }
  locationStatus = (courses: course[]) => {
    let count: number = 0;
    for (let i in courses) {
      if (this.props.application.apply?.selectedLocation.find(itm => itm.courseId === courses[i].course_id)) {
        count = count + 1;
      }
    }
    if (count === courses.length) { 
      return true
    } else {
      return false;
    }
  }
  submitApplication = () => {
    if (this.props.auth.user) {
      if (this.props.application.apply) {
        if (this.props.application.apply.selectedPosition) {
          this.props.createApplication(this.props.auth.user.user_id, {
            application_id: Math.random.toString(),
            application_date: new Date(),
            application_period: "2021",
            application_status: ApplicationStatus.Accepted,
            application_position_id: this.props.application.apply.selectedPosition.position_id,
            application_position: this.props.application.apply.selectedPosition,
            exams: this.props.application.apply.selectedLocation
          });
        }
      }
    }
  }
  render() {
    return (
      <Fragment>
      <div className="bg-white px-2 py-2 rounded shadow">
        <div className="bg-white flex flex-col mb-5 ml-2 mt-2">
          <div className="flex flex-row gap-3">
            <MdLocationOn className="text-8xl text-blue-600" />
            <div className="flex flex-col">
              <span className="text-3xl font-bold text-blue-600 mt-2">Choose exam location for each course</span>
              <span className="text-gray-500 font-bold text-lg">Position: {this.props.currentPosition?.position_title}</span>
              <span className="text-xs text-gray-500">For the corresponding exam course, choose the location where you want to do the exam, you can make changes anytime even after completing the process but before the exam date</span>
            </div>
          </div>
          
        </div>
        {this.props.application.apply && this.props.application.apply.selectedPosition && this.props.application.apply.selectedPosition.courses.map((item, i) => (
          <div onClick={() => this.setState({ selectedCourse: item })} key={i + 1}className={`${this.courseStatus(item) !== undefined ? "bg-blue-100 border border-blue-300": "bg-gray-200"} rounded px-2 py-2 flex flex-row justify-between items-center my-3`}>
            <div className="flex flex-row items-center gap-3">
              <div className={`${this.courseStatus(item) !== undefined ? "bg-blue-600" : "bg-gray-600"} h-8 w-8 rounded-full flex justify-center items-center text-white font-bold px-3 py-3`}>{i + 1}</div>
              <div className="flex flex-col">
                <span className={`text-lg font-bold ${this.courseStatus(item) !== undefined ? "text-blue-700": ""}`}>Course: {item.course_code}-{item.course_name}</span>
                <span className="text-xs text-gray-600">{this.courseStatus(item) !== undefined ? <span className="font-bold text-gray-700 text-sm flex"><MdLocationOn className="text-lg" /> Province: {this.courseStatus(item)?.location.province_name}, District: {this.courseStatus(item)?.location.district_name}, Center: {this.courseStatus(item)?.location.center_name}, Room: {this.courseStatus(item)?.location.room_name}</span> : <span className="italic text-sm text-gray-500">No location selected!</span>}</span>
              </div>
            </div>
            <div>
              <button onClick={() => this.setState({ centersModal: true })} className={`btn cursor-pointer ${this.courseStatus(item) !== undefined ? "bg-white text-blue-600 border border-blue-600" : "btn-blue"}`}>{this.courseStatus(item) !== undefined ? "Change" : "Choose"}</button>
            </div>
          </div>
        ))}
        <div className="flex flex-row justify-between items-center mx-2 my-2">
          <div className="flex flex-row items-center gap-2">
            {/* <button className="btn bg-white text-gray-600 border border-gray-300">Cancel</button> */}
            <button onClick={() => this.props.setApplicationStep(ApplicationStepsTypes.Step1)} className="btn bg-white text-gray-600 border border-gray-300">Back</button>
          </div>
          {this.props.application.apply && this.props.application.apply.selectedPosition && this.locationStatus(this.props.application.apply.selectedPosition.courses) === true &&
          <div onClick={this.submitApplication} className="btn bg-green-500 hover:bg-green-600 text-white cursor-pointer">Finish</div>}
        </div>
      </div>
      {this.state.centersModal === true && this.state.selectedCourse !== null &&
        <Modal
          title={<div className="text-gray-800 font-bold text-2xl">Choose exam center location</div>}
          backDrop={true}
          theme={Themes.default}
          close={() => this.setState({ centersModal: false })}
          backDropClose={false}
          widthSizeClass={ModalSize.maxWidth}
          displayClose={true}
          padding={{title: true,}} >
          {this.props.application.apply &&
          this.props.application.apply.selectedPosition &&
            <ChooseExamCenter
            positionCourses={this.state.selectedCourse}
            addLocation={this.props.addLocation}
            closeModal={this.closeModal}
          />}
        </Modal>
      }
      </Fragment>
    )
  }
}


const mapStateToProps = ({
  todos,
  auth,
  doExam,
  application,
}: StoreState): { todos: Todo[]; auth: Auth; doExam: examDataInterface, application: applicationStoreInterface } => {
  return { todos: todos, auth: auth, doExam: doExam, application: application };
};

const ExamCenter = connect(mapStateToProps, {
  fetchTodos,
  deleteTodo,
  fetchAuth,
  FC_GetTwoFactorAuth,
  FC_ChangeTwoFactorType,
  FC_Login,
  FC_LoginCheckCode,
  loadExam,
  addExamAnswer,
  deleteExamAnswers,
  SubmitExamAnswers,
  initiateApplication,
  selectPosition,
  createApplication,
  addLocation
})(_ExamCenter);

export default ExamCenter;