import React, { Component } from 'react'
import { MdAdd, MdSearch } from 'react-icons/md'
import { Link } from 'react-router-dom'
import * as Interfaces from '../../interfaces/index';
import { connect } from "react-redux";
import {
  Todo,
  Auth,
  fetchTodos,
  deleteTodo,
  fetchAuth,
  FC_GetTwoFactorAuth,
  FC_ChangeTwoFactorType,
  FC_Login,
  FC_LoginCheckCode,
  ActionTypes,
  examDataInterface,
  loadExam,
  addExamAnswer,
  deleteExamAnswers,
  SubmitExamAnswers,
  initiateApplication,
} from "../../actions";
import { StoreState } from "../../reducers";
import { applicationStoreInterface, initialApplicationInterface } from '../../actions/application';
import { ApplicationStepsTypes } from '../Application/ApplicationSteps/ApplicationSteps';
import { ApplicationInterface, application_exam } from '../../interfaces/application';
import Exams from './Exams';
import { PositionAPI } from '../../interfaces/index';

interface ApplicationsDashboardState {
  selectedPosition: PositionAPI | null;
  selectedExams: application_exam[] | null;
}
interface ApplicationsDashboardProps {
  auth: Auth;
  application: applicationStoreInterface;
  initiateApplication: (initial: initialApplicationInterface) => void;
}

export class _ApplicationsDashboard extends Component<ApplicationsDashboardProps, ApplicationsDashboardState> {
  constructor(props: ApplicationsDashboardProps) {
		super(props);

		this.state = {
      selectedPosition: null,
      selectedExams: null,
		};
	}
  componentDidMount = () => {
    if (this.props.application.apply === undefined) {
      this.props.initiateApplication({
        user_id: this.props.auth.user.user_id, 
        selectedPosition: null, 
        currentStep: ApplicationStepsTypes.Step1,
        positions: null,
        doTestStatus: {
          status: false,
        },
        selectedLocation: []
      });
    }
    if (this.props.application.applications.length > 0) {
      if (this.state.selectedPosition === null) {
        this.setState({ selectedPosition: this.props.application.applications[0].application_position })
      }
      if (this.state.selectedExams === null) {
        this.setState({ selectedExams: this.props.application.applications[0].exams })
      }
    }
  }
  render() {
    return (
      <div className="max-w-7xl mx-auto px-2 md:px-4 lg:px-24">
        <div className="bg-hero mt-5 rounded px-3 md:px-5 py-3 md:py-5">
          <div className="flex flex-col">
            <div className="flex flex-row justify-between items-center">
              <span className="text-blue-100 text-xl md:text-3xl lg:text-4xl mb-2 font-bold">My Applications</span>
              <div className="flex flex-row items-center gap-2">
                <span className="text-white">Year</span>
                <select className="input-white">
                  <option value="">2021</option>
                </select>
              </div>
            </div>
            <div className="grid grid-cols-2 md:grid-cols-2 lg:grid-cols-4 mt-5 gap-2 md:gap-5 lg:gap-10">
              <div className="col-span-1">
                <div className="flex flex-col">
                  <span className="text-xs md:text-sm font-light text-blue-50">Total Application(s)</span>
                  <span className="text-xl md:text-4xl font-bold text-blue-100">{this.props.application.applications.length < 10 ? "0" : ""}{this.props.application.applications.length}</span>
                </div>
              </div>
              <div className="col-span-1">
                <div className="flex flex-col">
                  <span className="text-xs md:text-sm font-light text-blue-100">Shortlisted Application(s)</span>
                  <span className="text-xl md:text-4xl font-bold text-blue-100">00</span>
                </div>
              </div>
              <div className="col-span-1">
                <div className="flex flex-col">
                  <span className="text-xs md:text-sm font-light text-blue-50">Rejected Application(s)</span>
                  <span className="text-xl md:text-4xl font-bold text-blue-50">00</span>
                </div>
              </div>
              <div className="col-span-1">
                <div className="flex flex-col">
                  <span className="text-xs md:text-sm font-light text-blue-50">Pending Application(s)</span>
                  <span className="text-xl md:text-4xl font-bold text-blue-50">00</span>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="my-5">
          <div className="grid grid-cols-12 gap-3">
            <div className="col-span-3">
              <div className="grid md:grid-cols-1 lg:grid-cols-1 gap-3">
                <Link to="/apply" className="px-2 py-2 bg-white rounded border border-blue-500 text-blue-600 flex flex-col items-center justify-center hover:bg-blue-100 cursor-pointer hover:text-blue-700 font-normal hover:font-bold">
                  <div>
                    <MdAdd className="text-7xl" />
                  </div>
                  <div className="mb-3">
                    <span>New Application</span>
                  </div>
                </Link>
                {this.props.application.applications.length === 0 ? 
                <div className="flex flex-col py-3 justify-center items-center text-gray-400 bg-white rounded">
                  <MdSearch className="text-7xl text-gray-300" />
                  <span className="text-lg">No Applications yet!</span>
                </div> :
                this.props.application.applications.map((item, i) => (
                  <div onClick={() => {this.setState({ selectedPosition: item.application_position }); this.setState({ selectedExams: item.exams })}} className={`px-3 py-3 bg-white hover:bg-blue-50 hover:border-blue-600 rounded border ${this.state.selectedPosition?.position_id === item.application_position.position_id ? "border-blue-600 bg-blue-100" : "border-gray-200 bg-white"} flex flex-col cursor-pointer font-normal hover:font-bold`}>
                    <span className="font-bold text-blue-600">{item.application_position.position_title}</span>
                    <div className="flex flex-col text-xs text-gray-600 mt-3">
                      <div className="flex flex-row gap-2">
                        <span className="text-gray-500">Application date:</span>
                        <span className="text-black font-bold">02 August 2021</span>
                      </div>
                      <div className="flex flex-row gap-2">
                        <span className="text-gray-500">Application period:</span>
                        <span className="text-black font-bold">{item.application_period}</span>
                      </div>
                      <div className="flex flex-row gap-2">
                        <span className="text-gray-500">Application status:</span>
                        <span className="text-black font-bold">{item.application_status}</span>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            </div>
            <div className="col-span-9">
              {this.state.selectedPosition !== null && this.state.selectedExams !== null &&
              <div className="bg-white">
                <Exams 
                  selectedExams={this.state.selectedExams}
                  selectedPosition={this.state.selectedPosition}
                />
              </div>}
            </div>
          </div>
        </div>
      </div>
    )
  }
}

const mapStateToProps = ({
  todos,
  auth,
  doExam,
  application,
}: StoreState): { todos: Todo[]; auth: Auth; doExam: examDataInterface, application: applicationStoreInterface } => {
  return { todos: todos, auth: auth, doExam: doExam, application: application };
};

const ApplicationsDashboard = connect(mapStateToProps, {
  fetchTodos,
  deleteTodo,
  fetchAuth,
  FC_GetTwoFactorAuth,
  FC_ChangeTwoFactorType,
  FC_Login,
  FC_LoginCheckCode,
  loadExam,
  addExamAnswer,
  deleteExamAnswers,
  SubmitExamAnswers,
  initiateApplication
})(_ApplicationsDashboard);

export default ApplicationsDashboard;